/* @import url('https://fonts.googleapis.com/css2?family=Playfair+Display&family=Poppins:wght@300;400;500;600&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Berkshire+Swash&family=Playfair+Display&family=Poppins:wght@400;500;600&display=swap');
* {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}
:root {
    --color-bg: #0A192F;
    --color-bg-variant: #112240;
    --color-primary: #A9FFFF;
    --color-primary-variant: rgba(121, 177, 216, 0.4);
    --color-white: #FFFFFF;
    --color-purple: #BD11E0;
    --color-light: rgba(203, 221, 243, 0.443);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}
html {
    scroll-behavior: smooth;
}
::-webkit-scrollbar{
    display: none;
}

body{
    /* font-family: 'Poppins', sans-serif; */
    font-family: 'Berkshire Swash', cursive;
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    background-image: url(../src/assets/bg-texture.png);
}

/* =====General styles===== */
.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1, h2, h3, h4, h5{
    font-weight: 500;
}

h1 {
    font-size: 2.5rem;
}
section {
    margin-top: 8rem ;
}
section > h2, section > h5{
    text-align: center;
    color: var(--color-light);
}
section > h2 {
    color:var(--color-primary);
    margin-bottom: 3rem;
}
.fade-in {
    opacity: 0;
    animation: fade-in 2s forwards;
  }
  
  @keyframes fade-in {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  
.text-light{
    color: var(--color-light)
}
.text-blue {
    color: var(--color-primary);
}
.text-purple{
    color: var(--color-purple);
}
a{
    color: var(--color-primary);
    transition: var(--transition);
}
a:hover {
    color: var(--color-white);
}
.btn {
    width: max-content;
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary{
    background: var(--color-primary);
    color: var(--color-bg);
}
img {
    display:block;
    width: 100%;
    object-fit: cover;
}
/* =====Media queries (medium) =====*/
@media screen and (width: 1280px) {
    .container{
        width: var(--container-width-sm);
    }
    
    section{
        height: 100vh;
    }
}
/* =====Media queries (medium) =====*/
@media screen and (max-width: 1023px) {
    .container{
        width: var(--container-width-md);
    }
    
    section{
        height: 140vh;
        margin-top: 6rem;
    }
}
/* =====Media queries (medium) =====*/
@media screen and (max-width: 1024px) {
    .container{
        width: var(--container-width-md);
    }
    
    section{
        height: fit-content;
        margin-top: 3rem;
    }
}

/* =====Media queries (small) =====*/
@media screen and (max-width: 600px) {
    .container{
        width: var(--container-width-md);
    }
    section{
        height: fit-content;
    }
    section > h2{
        margin-bottom: 2rem;
    }
}
